<template>
  <div class="card">
    <div class="card-body">
      <h3 class="text-dark">
        <strong>Ubah Data Pekerjaan</strong>
      </h3>
      <br />
      <form>
        <div class="form-group">
          <label for="stkerja">Status Pekerjaan</label>
          <select class="form-control" id="stkerja">
            <option>Bekerja</option>
            <option>Tidak Bekerja</option>
          </select>
        </div>
        <div class="p-2 mb-2 bg-biru-accent text-dark">
          *Isi kolom identitas wali berikut jika ada.
        </div>
        <div class="form-group">
          <label for="nmPerusahaan">Nama Perusahaan</label>
          <input
            type="text"
            class="form-control"
            id="nmPerusahaan"
            placeholder="Nama Perusahaan"
            v-model="form.company_name"
          />
        </div>
        <div class="form-group">
          <label for="jenisUsaha">Jenis Badan Usaha</label>
          <input
            type="text"
            class="form-control"
            id="jenisUsaha"
            placeholder="Nama Perusahaan"
            v-model="form.company_category_id"
          />
        </div>
        <div class="form-group">
          <label for="inputAddress">Alamat Perusahaan</label>
          <input
            type="text"
            class="form-control"
            id="inputAddress"
            placeholder="Alamat Perusahaan"
            v-model="form.address"
          />
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputProv">Provinsi</label>
            <select
              id="inputProv"
              class="form-control"
            >
              <option disable v-if="getDataProvinsi.province == null">
                Pilih Provinsi
              </option>
              <option disable v-else>{{ getDataProvinsi.province }}</option>
              <option
                v-for="provinsi in getDataProvinsi"
                :key="provinsi.province_id"
                :value="provinsi.province_id"
              >
                {{ provinsi.province }}
              </option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="inputKokab">Kota/Kabupaten</label>
            <select id="inputKokab" class="form-control">
              <option selected>Pilih Kota/Kabupaten...</option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputHp">Telepon</label>
            <input
              type="text"
              class="form-control"
              id="inputHp"
              placeholder="Telepon"
              :value="getDataStatus.telephone_number"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="inputPostal">Kode Pos</label>
            <input
              type="text"
              class="form-control"
              id="inputPostal"
              placeholder="Kode Pos"
              :value="getDataStatus.postal"
            />
          </div>
        </div>
        <div class="form-group">
          <label for="inputJabatan">Jabatan</label>
          <input
            type="text"
            class="form-control"
            id="inputJabatan"
            placeholder="Jabatan"
            :value="getDataStatus.position"
          />
        </div>
        <div class="form-group">
          <label for="inputBidangUsh">Bidang Usaha</label>
          <input
            type="text"
            class="form-control"
            id="inputBidangUsh"
            placeholder="Bidang Usaha"
            :value="getDataStatus.job_field"
          />
        </div>
        <button type="submit" class="btn btn-primary btn-success">
          SIMPAN
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "DataPekerjaan",
  data() {
    return {
      provinsi: [],
      form: {
        company_name: null,
        company_category_id: null,
        job_field_id: null,
        position: null,
        telephone_number: null,
        address: null,
        province_id: null,
        city_id: null,
        districts_id: null,
        village_id: null,
        rt: null,
        rw: null,
        postal: null,
      },
    };
  },
  computed: {
    ...mapGetters({
      getDataStatus: "stkerja/datastkerja",
      getDataProvinsi: "provinsi/dataprovinsi",
      getUserNpm: "authentication/users"
    }),
  },
  methods: {
    ...mapActions({
      datastatus: "stkerja/stkerja",
      dtprovinsi: "provinsi/provinsi",
    }),
    getDTKERJA() {
      this.datastatus();
    },
    getPilihProvinsi() {
      this.dtprovinsi();
    },
    async submit() {
      try {
        
        let dataWork = {
          company_name: this.form.company_name,
          company_category_id: this.form.company_category_id,
          job_field_id: this.form.job_field_id,
          position: this.form.position,
          telephone_number: this.form.telephone_number,
          address: this.form.address,
          province_id: this.form.province_id,
          city_id: this.form.city_id,
          districts_id: this.form.districts_id,
          village_id: this.form.village_id,
          rt: this.form.rt,
          rw: this.form.rw,
          postal: this.form.postal,
        };
        let response = await axios.put("/oasis/job", dataWork);

        // this.setRiwayatToView();
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        console.warn(error);
        
      }
    },
  },
  mounted() {
    this.getDTKERJA();
    this.dtprovinsi();
  },
};
</script>

